.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  transition: opacity 500ms;
  visibility: hidden;
  opacity: 0;
  z-index: 10000;

  .popup {
    margin: 0 auto;
    padding: 0;
    width: 100%;
    max-width: 1000px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @media (min-width: 1024px) {
      .popup .close {
        top: -36px;
        right: -36px;
      }
    }

    .close {
      position: absolute;
      top: -36px;
      right: -33px;
      font-size: 30px;
      text-decoration: none;
      color: #fff;
      background: #818181;
      height: 36px;
      width: 36px;
      line-height: 36px;
      border-radius: 50%;
      text-align: center;
    }

    @media screen and (max-width: 1023px), screen and (max-height: 700px) {
      .popup .content {
        max-height: 75vh !important;
      }
    }

    .content {
      max-height: 100%;
      overflow: auto;
      margin: 10px;
      background: #fff;
      border-radius: 5px;
    }

    iframe {
      max-height: 915px !important;
      border: 0;
    }
  }
}

